.form {
  width: 280px;
  margin-top: 44px;
  margin-bottom: 44px;
  margin-left: auto;
  margin-right: auto;
  padding: 40px 12px;
  border-radius: 20px;
  background-color: #ffffff;
  box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
}

@media screen and (min-width: 768px) {
  .form {
    width: 608px;
    margin-top: 80px;
    margin-bottom: 80px;
    padding: 60px 80px 40px 80px;
    border-radius: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .form {
    margin-top: 44px;
    padding: 60px 80px;
  }
}

.title {
  text-align: center;
  font-weight: 500;
  font-size: 24px;
  line-height: 1.38;
  letter-spacing: 0.04em;
  margin-bottom: 40px;
  color: #111111;
}

@media screen and (min-width: 768px) {
  .title {
    font-size: 36px;
    line-height: 1.36;
  }
}

.questionText {
  font-weight: 400;
  font-size: 12px;
  line-height: 1.33;
  text-align: center;
  letter-spacing: 0.04em;
  color: #888888;
}

.registerLink {
  display: inline-block;
  color: #54adff;
}

.buttonContainer {
  margin-top: 40px;
  margin-bottom: 24px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 10px 28px;
  width: 100%;
  height: 48px;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0.04em;
  border: 2px solid #54adff;
  border-radius: 40px;
  color: #ffffff;
  background-color: #54adff;
}

.button:hover {
  background: linear-gradient(290.46deg, #419ef1 0%, #9bd0ff 107.89%);
  color: #ffffff;
  cursor: pointer;
}

.myСomponent.dark {
  background-color: #165158;

  background-blend-mode: multiply;
  background-image: url('../../images/bg-mobile.jpg');
  background-position: center;
  background-size: contain;
}

@media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .myСomponent.dark {
    background-image: url('../../images/bg-mobile2xjpg.jpg');
  }
}

@media screen and (min-width: 768px) {
  .myСomponent.dark {
    background-blend-mode: color-burn;
    background-image: url('../../images/bg-tablet.jpg');
  }

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    .myСomponent.dark {
      background-image: url('../../images/bg-tablet2x.jpg');
    }
  }
}

@media screen and (min-width: 1280px) {
  .myСomponent.dark {
    background-image: url('../../images/bg-desctop.jpg');
  }

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    .myСomponent.dark {
      background-image: url('../../images/bg-desctop2x.jpg');
    }
  }
}

.myСomponent.dark .form {
  background-color: #467c82;
  color: #d9d2d2;
}

.myСomponent.dark .title {
  color: #d9d2d2;
}

.myСomponent.dark .button {
  color: #d9d2d2;
  border: 1px solid #64999e;
  background-color: #417075;
  transition: background 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.myСomponent.dark .button:hover {
  background: linear-gradient(290.46deg, #55787c 0%, #64999e 107.89%);
  color: #d9d2d2;
}
